<template>
    <div class="header-container">
        <div class="l-contend">
            <el-button
                style="margin-right: 20px"
                @click="handleMenu"
                icon="el-icon-menu"
                size="mini"
            ></el-button>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item
                    v-for="item in tags"
                    :key="item.path"
                    :to="{ path: item.path }"
                    >{{ item.label }}</el-breadcrumb-item
                >
            </el-breadcrumb>
        </div>
        <div class="r-contend">
            <el-dropdown @command="handleClick">
                <span class="el-dropdown-link">
                    <img class="userimg" src="../assets/头像.jpg" alt="" />
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>个人中心</el-dropdown-item>
                    <el-dropdown-item command="cancel">退出</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import Cookie from "js-cookie";
import { nextTick } from 'vue';
export default {
    name: "CommonHeader",
    methods: {
        handleMenu() {
            this.$store.commit("collapseMenu");
        },
        handleClick(command) {
            if (command === 'cancel') {
                console.log('登出');
                //清除token
                Cookie.remove('token')
                //清除cookie中的menu
                Cookie.remove('menu')
                //跳转到登陆页面
                this.$router.push('/login')
            }
        },
    },
    computed: {
        ...mapState({
            tags: (state) => state.tab.tabsList,
        }),
    },
};
</script>

<style lang="less" scoped>
.header-container {
    padding: 0 20px;
    background: #333;
    height: 60px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    .text {
        color: #fff;
        font-size: 14px;
        margin-left: 10px;
    }
    .r-contend {
        .userimg {
            height: 40px;
            width: 40xp;
            border-radius: 50%;
        }
    }
    .l-contend {
        display: flex;
        align-items: center;
        /deep/.el-breadcrumb__item {
            .el-breadcrumb__inner {
                font-weight: normal;
                &,
                is-link {
                    color: #666;
                }
            }
            &:last-child {
                .el-breadcrumb__inner {
                    color: #fff;
                }
            }
        }
    }
}
</style>