<template>
    <el-row>
        <el-col :span="8" style="padding-right: 10px">
            <el-card class="box-card">
                <div class="user">
                    <img src="../assets/头像.jpg" alt="" />
                    <div class="userInfo">
                        <p class="name" v-show="show">Admin</p>
                        <p class="name" v-show="!show">Xiaoxiao</p>
                        <p class="access" v-show="show">超级管理员</p>
                        <p class="access" v-show="!show">用户</p>
                    </div>
                </div>
                <div class="login-info">
                    <p>当前登录时间：<span>{{getfmtTime()}}</span></p>
                    <p>上次登录地点：<span>河北</span></p>
                </div>
            </el-card>
            <el-card style="margin-top: 20px; height: 460px">
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column
                        v-for="(val, key) in tableLabel"
                        :prop="key"
                        :label="val"
                        :key="key"
                    />
                </el-table>
            </el-card>
        </el-col>
        <el-col :span="16" style="padding-left: 10px">
            <div class="num">
                <el-card
                    v-for="item in countData"
                    :key="item.name"
                    :body-style="{ display: 'flex', padding: '0' }"
                >
                    <i
                        class="icon"
                        :class="`el-icon-${item.icon}`"
                        :style="{ background: item.color }"
                    ></i>
                    <div class="detail">
                        <p class="price">￥{{ item.value }}</p>
                        <p class="desc">{{ item.name }}</p>
                    </div>
                </el-card>
            </div>
            <el-card style="height: 300px">
                <!-- 折线图 -->
                <div ref="echarts1" style="height: 280px"></div>
            </el-card>
            <div class="graph">
                <el-card style="height: 260px">
                    <!-- 柱状图 -->
                    <div ref="echarts2" style="height: 260px"></div>
                </el-card>
                <el-card style="height: 260px">
                    <!-- 饼状图 -->
                    <div ref="echarts3" style="height: 240px"></div>
                </el-card>
            </div>
        </el-col>
    </el-row>
</template>

<script>
import { getData } from "../api";
import * as echarts from "echarts";
import dayjs from 'dayjs'

export default {
    name: "Home",
    data() {
        return {
            tableData: [],
            tableLabel: {
                name: "型号",
                todayBuy: "今日销量",
                monthBuy: "月销量",
                totalBuy: "总销量",
            },
            countData: [
                {
                    name: "今日支付订单",
                    value: 1234,
                    icon: "success",
                    color: "#2ec7c9",
                },
                {
                    name: "今日收藏订单",
                    value: 210,
                    icon: "star-on",
                    color: "#ffb980",
                },
                {
                    name: "今日未支付订单",
                    value: 1234,
                    icon: "s-goods",
                    color: "#5ab1ef",
                },
                {
                    name: "本月支付订单",
                    value: 123224,
                    icon: "success",
                    color: "#2ec7c9",
                },
                {
                    name: "本月收藏订单",
                    value: 2102,
                    icon: "star-on",
                    color: "#ffb980",
                },
                {
                    name: "本月未支付订单",
                    value: 12354,
                    icon: "s-goods",
                    color: "#5ab1ef",
                },
            ],
        };
    },
    //输出时间
    methods: {
        getfmtTime() {
            return dayjs().format("YYYY-MM-DD HH:mm:ss");    
        },
    },
    mounted() {
        getData().then(({ data }) => {
            const { tableData } = data.data;
            console.log(data.data);
            console.log(tableData);
            this.tableData = tableData;

            //折线图
            // 基于准备好的dom，初始化echarts实例
            const echarts1 = echarts.init(this.$refs.echarts1);
            const { orderData, userData, videoData } = data.data;
            console.log(orderData);
            const xData = Object.keys(orderData.data[0]);
            // 指定图表的配置项和数据
            var echarts1Option = {
                tooltip: {},
                legend: {
                    data: Object.keys(orderData.data[0]),
                },
                xAxis: {
                    data: Object(orderData.date),
                },
                yAxis: {},
                series: [],
            };
            xData.forEach((key) => {
                echarts1Option.series.push({
                    name: key,
                    data: orderData.data.map((item) => item[key]),
                    type: "line",
                });
            });

            // 使用刚指定的配置项和数据显示图表。
            echarts1.setOption(echarts1Option);

            //折线图
            // 基于准备好的dom，初始化echarts实例
            const echarts2 = echarts.init(this.$refs.echarts2);
            const echarts2Option = {
                legend: {
                    // 图例文字颜色
                    textStyle: {
                        color: "#333",
                    },
                },
                grid: {
                    left: "20%",
                },
                // 提示框
                tooltip: {
                    trigger: "axis",
                },
                xAxis: {
                    type: "category", // 类目轴
                    data: userData.map((item) => item.date),
                    axisLine: {
                        lineStyle: {
                            color: "#17b3a3",
                        },
                    },
                    axisLabel: {
                        interval: 0,
                        color: "#333",
                    },
                },
                yAxis: [
                    {
                        type: "value",
                        axisLine: {
                            lineStyle: {
                                color: "#17b3a3",
                            },
                        },
                    },
                ],
                color: ["#2ec7c9", "#b6a2de"],
                series: [
                    {
                        name: "新增用户",
                        data: userData.map((item) => item.new),
                        type: "bar",
                    },
                    {
                        name: "活跃用户",
                        data: userData.map((item) => item.active),
                        type: "bar",
                    },
                ],
            };
            // 使用刚指定的配置项和数据显示图表。
            echarts2.setOption(echarts2Option);

            //饼状图
            // 基于准备好的dom，初始化echarts实例
            const echarts3 = echarts.init(this.$refs.echarts3);
            var echarts3Option = {
                tooltip: {
                    trigger: "item",
                },
                series: [
                    {
                        type: "pie",
                        data: videoData,
                    },
                ],
            };
            // 使用刚指定的配置项和数据显示图表。
            echarts3.setOption(echarts3Option);
        });
    },
    computed:{
        show(){
            return this.$store.state.tab.show
        }
    }
};
</script>

<style lang="less" scoped>
.user {
    display: flex;
    align-items: center; //垂直居中
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc; //下内边距作为分隔线
    img {
        height: 150px;
        width: 150px;
        border-radius: 50%;
        margin-right: 40px;
    }
    .userInfo {
        .name {
            font-size: 32px;
            margin-bottom: 10px;
        }
        .access {
            color: #999999;
        }
    }
}
.login-info {
    p {
        line-height: 28px;
        font-size: 14px;
        color: #999999;
        span {
            color: #666666;
            margin-left: 60px;
        }
    }
}
.num {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; //两边贴边

    .el-card {
        width: 32%;
        margin-bottom: 20px;
    }
    .icon {
        height: 80px;
        width: 80px;
        font-size: 30px;
        text-align: center;
        line-height: 80px;
        color: #fff;
    }
    .detail {
        display: flex;
        flex-direction: column; //纵向
        justify-content: center;
        margin-left: 15px;
        .price {
            font-size: 30px;
            margin-top: 0;
            margin-bottom: 10px;
            line-height: 30px;
            height: 30px;
        }
        .desc {
            font-size: 14px;
            color: #999;
            text-align: center;
            margin: 0;
        }
    }
}
.graph {
    display: flex;
    justify-content: space-between;
    .el-card {
        width: 48%;
        margin-top: 20px;
    }
}
</style>
